\n\n.text-node-id\n  font-size: 11px\n  color: $color-default\n\n.text-socket\n  font-size: 11px\n  font-color: $color-default\n  text-shadow: .25px .5px #00000022\n  -webkit-text-stroke-width: .2px\n  -webkit-text-stroke-color: white\n\n  &.critical-path\n    font-color: $critical-path-highlight\n\n.button\n  height: 100%\n  margin-right: 5px\n  color: $color-default-dark\n  border: 5px outset\n        \n  &:focus\n      border: inset\n\n.pixelated\n    image-rendering: pixelated\n\n@-moz-document url-prefix()\n.pixelated\n    image-rendering: crisp-edges\n\n.minecraft-background\n  background-image: url(\"./assets/images/Background.png\")\n  background-repeat: repeat\n  background-blend-mode: multiply\n\n.scrollable\n  overflow-y: auto\n\n/* Importing Bootstrap SCSS file. */\n@import '~bootstrap/scss/bootstrap'\n\n/* Importing Bootstrap SCSS file. */\n@import '~bootstrap/scss/bootstrap'\n","@import ../rete_variables\n\n// All are define important to override the inline styling that is given to them\n.socket\n    display: inline-block !important\n    cursor: pointer !important\n    border: 1px solid $socket-border-color !important\n    border-radius: $socket-size / 2 !important\n    width: $socket-size !important\n    height: $socket-size !important\n    margin: $socket-margin !important\n    vertical-align: middle !important\n    z-index: 2 !important\n    background: $socket-color !important\n    box-sizing: border-box !important\n    &:hover\n        border-width: 4px !important\n\n    &.output\n        margin-right: -$socket-size / 2 !important\n\n    &.input\n        margin-left: -$socket-size / 2 !important\n\n    &.output, &.input\n        &.Stone\n            background: get-item-color(Stone) !important\n            border-color: get-item-border(Stone) !important\n        &.Grass\n            background: get-item-color(Grass) !important\n            border-color: get-item-border(Grass) !important\n        &.Dirt\n            background: get-item-color(Dirt) !important\n            border-color: get-item-border(Dirt) !important\n        &.Cobblestone\n            background: get-item-color(Cobblestone) !important\n            border-color: get-item-border(Cobblestone) !important\n        &.Planks\n            background: get-item-color(Planks) !important\n            border-color: get-item-border(Planks) !important\n        &.Logs\n            background: get-item-color(Logs) !important\n            border-color: get-item-border(Logs) !important","// ########## Global Layout Settings ##########\n$grid-width: 75%\n$grid-height: 60%\n\n// Spacings between modules\n$module-margin-left: 10px\n$module-margin-top: $module-margin-left\n\n$output-width: calc(100% - #{$grid-width})\n$output-height: calc(100% - 2*#{$module-margin-top})\n    \n$button-height: 50px\n\n// Delete specific button settings\n$delete-button-width: 150px\n$delete-button-height: 50px\n\n// Inventory Widget height\n$resource-children-height: calc(100% - 3*#{$module-margin-top})\n\n$border-thickness: 5px\n\n// ########## Color Definitions ##########\n$custom-selected:#F0ECFD\n$custom-highlight:#C4B5F6\n$output-selected: #CAF3F6\n$output-highlight:#2ED0DB\n$crafting-selected:#F6EDCA\n$crafting-highlight:#DBB62E\n$resource-selected:#D8FCCE\n$resource-highlight:#66F23F\n$minecraft-selected: #acb6c3\n$minecraft-highlight: #A8AAB3\n\n$color-default:#707070\n$color-default-dark:#2A2A2A\n$color-default-highlight:#CCCCCC\n\n// Pinstripe Definitions\n$critical-path-selected:#FFDAD0\n$critical-path-highlight:#FF6F45\n$critical-path-highlight-alpha: rgba( $critical-path-highlight, .5)\n\n\n// ########## Sprite Rendering Settings\n$sprite-size: 32px\n$sprite-scale: 2.2\n\n// In number of sprites\n$sprite-sheet-horizontal-dimensions: 16\n$sprite-sheet-vertical-dimensions: 35\n\n$sprite-width: calc(#{$sprite-size * $sprite-scale})\n$sprite-sheet-width: calc( #{$sprite-size * $sprite-sheet-horizontal-dimensions * $sprite-scale})\n$sprite-sheet-height: calc( #{$sprite-size